.header {
  display: flex;
  justify-content: space-between;
}
.menu {
  display: none;
}
.logo {
  height: 8rem;
}
.header-menu {
  display: flex;
  list-style: none;
  gap: 2rem;
  color: white;
}
.header-menu > li {
  font-size: 1.5rem;
  font-weight: 500;
}
.header-menu > li:hover {
  cursor: pointer;
  color: var(--orange);
}
@media screen and (max-width: 768px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
  .header > img {
    width: 10rem;
  }

  .header-menu {
    display: none;
  }
}
